// 引入公用存储用户登录
import { login } from '../common/list.js';
// 引入即可反馈组件
import HMmessages from '@/components/HM-messages/HM-messages.vue';
export default {
	name: 'modals',
	components: { HMmessages },
	data() {
		return {
			modaishow: false,
			messages: ''
		};
	},
	methods: {
		// 取消模态框
		messcancel() {
			this.modaishow = false;
		},

		init(message) {
			this.modaishow = true;
			this.messages = message;
		},

		// 发起登录取到用户信息
		getUserInfo(event) {
			console.log(event)
			this.modaishow = false
			//存储用户信息到数据库
			let user = event.detail.userInfo
			login(user)
			.then((res)=>{
				console.log(res)
				//登录成功
				this.HMmessages.show('登录成功',{
					icon:'success',
					iconColor:'#ffffff',
					fontColor:'#ffffff',
					background:'rgba(102,0,51,0.8)'
				})
			})
			.catch((err)=>{
				console.log(err)
			})
		}
	}
};
